import Ball from './Ball'
import Paddle from './Paddle'

const ball = new Ball(document.getElementById("ball"))
const playerLeftPaddle = new Paddle(document.getElementById("player-left"))
const playerRightPaddle = new Paddle(document.getElementById("player-right"))
const leftScore = document.getElementById("score-left")
const RightScore = document.getElementById("score-right")
let lastTime;

function update(time)
{
    const leftRect = playerLeftPaddle.rect()
    const rightRect = playerRightPaddle.rect()
    if(lastTime != null)
    {
        const delta = time-lastTime
        ball.update(delta, leftRect, rightRect)
        
    }
    lastTime = time

    const rect = ball.rect()
    if(rect.right > window.innerWidth || rect.left < 0)
    {
        if(rect.left <= 0)
        RightScore.textContent = parseInt(RightScore.textContent) + 1
        else
        leftScore.textContent = parseInt(leftScore.textContent) + 1
        ball.reset()
    }
    
    window.requestAnimationFrame(update)
}

document.onkeydown = movePad

function movePad(e){
    if(e.key == "ArrowDown" && playerRightPaddle.position < 100)
    playerRightPaddle.position += 5
    if(e.key === "ArrowUp" && playerRightPaddle.position >= 10)
    playerRightPaddle.position -= 5
    if(e.key == 'w' && playerLeftPaddle.position > 10)
    playerLeftPaddle.position -= 3
    if(e.key == 's' && playerLeftPaddle.position < 100)
    playerLeftPaddle.position += 3
}


window.requestAnimationFrame(update)