const INITIAL_VELOCITY = 0.04;
const vel = 0.000001

export default class Ball{
    constructor(ballElem){
        this.ballElem = ballElem
        this.reset()
    }

    get x(){
        return parseFloat(getComputedStyle(this.ballElem).getPropertyValue("--x"))
    }

    set x(value){
        this.ballElem.style.setProperty("--x", value)
    }

    get y(){
        return parseFloat(getComputedStyle(this.ballElem).getPropertyValue("--y"))
    }

    set y(value){
        this.ballElem.style.setProperty("--y", value)
    }


    reset()
    {
        this.x = 50;
        this.y = 50;
        this.direction = {x:0, y:0};
        
        while(Math.abs(this.direction.x) <= 0.2 || Math.abs(this.direction.x) >=0.8){
            const heading = randomNumberBetween(0, 2*Math.PI)
            this.direction = { x: Math.cos(heading), y:Math.sin(heading)}
        }
        
        this.velocity = INITIAL_VELOCITY;
    }

    rect(){
        return (this.ballElem.getBoundingClientRect())
    }
    update(delta, leftRect, rightRect)
    {
        this.x += this.direction.x * this.velocity * delta
        this.y += this.direction.y * this.velocity * delta 

        this.velocity += vel 

        let rect = this.rect()
        if(rect.bottom >= window.innerHeight || rect.top <= 0)
        this.direction.y *= -1;
        
        if(rect.left <=leftRect.right && rect.top <=leftRect.bottom && rect.bottom >= leftRect.top)
        this.direction.x *= -1;
        if(rect.right >= rightRect.left && rect.top <=rightRect.bottom && rect.bottom >= rightRect.top)
        this.direction.x *= -1
    }

}

function randomNumberBetween(min, max)
{
    return Math.random() * (max - min) + min;
}
